<template>
  <div class="profile">
    <Modal :modalFlag="modalFlag" :handleOk="handleOk" :handleCancel="handleCancel"/>
    <PayModal :modal='modal' :noPay="noPay" :payment="payment" :cb="cb"/>
    <div class="step-img"></div>
    <div class="user-info">
      <div class="location-name">{{ info.deviceInfo.areaname }}</div>
      <div class='line'></div>
      <div class="car-coin">
        我的车币余额 <span style='color:#EE6969'>{{ parseFloat(info.userInfo.money/100).toFixed(2) }}</span> 元
      </div>
    </div>
    <div class="meal">套餐选择</div>
    <div class="meal-items">
      <div class="meal-item" v-for="v in info.deviceInfo.package">{{ v.name }}</div>
    </div>
    <div class="tag">
      <div class="tag-txt">提示：</div>
      <div class="tag1">1.暂停不计时，暂停超过5分钟将结束本次服务</div>
      <div class="tag1">2.机器启动后金额不能退还，请按照操作步骤洗车</div>
      <div class='untying' @click="unbind" v-if="info.clienttype=='wx'">解绑账号</div>
    </div>
    <div class='money'>
        共计 ￥{{ parseFloat(info.deviceInfo.price/100).toFixed(2) }}
    </div>
    <div class='gopay' @click='goPay'>确认支付</div>
    <Loading v-if="loading"/>
  </div>
</template>
<script>
import PayModal from '@/components/PayModa'
import axios from 'axios';
import Loading from '@/components/Loading'
import Modal from '@/components/Modal'
import qs from 'qs';
import Cookies from 'js-cookie'

export default {
    data(){
        return{
            apiUrl: 'http://ian.chemi.ren/cmapi/public',
            modal:false,
            loading: false,
            modalFlag:false,
            cb: 0,
            info: {
              deviceInfo: {
                areaname: '',
                package: [],
                price: 0
              },
              userInfo: {
                money: 0
              },
              clienttype: ''
            },

            parameter: {}
        }
    },
    methods:{
        handleOk(){
          this.modalFlag = false;
          this.loading = true;
          // 判断authcode是否存在
          if(!this.parameter.authcode){
            axios({
              url: this.apiUrl + '/xiche/getAuthCode',
              data: qs.stringify({ajax: 1}),
              method: 'post',
              timeout: 10000
            }).then(res=>{
              if(res.data.errorcode !== 0){
                this.loading = false;
                return this.$toast(res.data.message);
              }
              this.parameter.authcode = res.data.data.authcode;
              axios({
                url: this.apiUrl + '/xiche/unbind',
                data: qs.stringify({ajax: 1}),
                method: 'post',
                timeout: 10000
              }).then(res=>{
                this.loading = false;
                if(res.data.errorcode !== 0){
                  return this.$toast(res.data.message);
                }
                Cookies.remove('token');
                this.$toast('解绑成功');
                setTimeout(()=>{
                  this.$router.push({
                    path:'/',
                    query: {devcode: this.parameter.devcode, authcode: this.parameter.authcode}
                  });
                }, 1000);
              }).catch(res=>{
                this.loading = false;
                this.$toast('网络错误');
              });

            }).catch(res=>{
              this.loading = false;
              this.$toast('网络错误');
            });

          } else {

            axios({
              url: this.apiUrl + '/xiche/unbind',
              data: qs.stringify({ajax: 1}),
              method: 'post',
              timeout: 10000
            }).then(res=>{
              this.loading = false;
              if(res.data.errorcode !== 0){
                return this.$toast(res.data.message);
              }
              Cookies.remove('token');
              this.$toast('解绑成功');
              setTimeout(()=>{
                this.$router.push({
                  path:'/',
                  query: {devcode: this.parameter.devcode, authcode: this.parameter.authcode}
                });
              }, 1000);
            }).catch(res=>{
              this.loading = false;
              this.$toast('网络错误');
            });
          }
        },
        handleCancel(){
          this.modalFlag=false
        },
        goPay(){
            this.modal=true
        },
        noPay(){
            this.modal=false
        },
        payment(payway){
          if(payway == 'wxpay' && this.info.clienttype != 'wx' && this.info.clienttype != 'cm'){
            return this.$toast('当前支付环境不支持在线支付');
          }
          if(payway == 'cbpay' && this.info.deviceInfo['price'] > this.info.userInfo['money']){
            return this.$toast('余额不足');
          }
          if(this.loading){
            return this.$toast('支付中');
          }
          // 开始支付
          this.loading = true;
          var query = {payway: payway, devcode: this.parameter.devcode, ajax: 1};
          // 1
          axios({
            url: this.apiUrl + '/xiche/createCard',
            data: qs.stringify(query),
            method: 'post',
            timeout: 10000
          }).then(card=>{

            if(card.data.errorcode !== 0){
              this.loading = false;
              return this.$toast(card.data.message);
            }
            // 2
            axios({
              url: this.apiUrl + '/xiche/payQuery',
              data: qs.stringify({tradeid:card.data.data.tradeid,ajax:1}),
              method: 'post',
              timeout: 10000
            }).then(res=>{

              if(res.data.errorcode === 0){
                this.loading = false;
                this.$toast(res.data.message);
                setTimeout(()=>{
                  this.$router.push({
                    path:'/payok',
                    query:{tradeid:card.data.data.tradeid}
                  });
                }, 1000);
                return false;
              }
              if(this.info.clienttype == 'cm'){
                // 车秘APP支付
                return this.cmAppPay(card.data.data.tradeid);
              }
              // 3
              axios({
                url: this.apiUrl + '/wxpayjs/api',
                data: qs.stringify({tradeid:card.data.data.tradeid,ajax:1}),
                method: 'post',
                timeout: 10000
              }).then(wxsdk=>{

                this.loading = false;
                if(wxsdk.data.errorcode !== 0){
                  return this.$toast(wxsdk.data.message);
                }
                // 4
                this.chooseWXPay(wxsdk.data.data,res=>{
                  this.$toast('支付完成');
                  setTimeout(()=>{
                    this.$router.push({
                      path:'/payok',
                      query:{tradeid:card.data.data.tradeid}
                    });
                  }, 1000);
                },res=>{
                  this.$toast('支付失败');
                },res=>{
                  if(res['errMsg'] == 'chooseWXPay:fail' || res['err_msg'] == 'get_brand_wcpay_request:fail') {
                    this.$toast('支付失败,请重试!' + JSON.stringify(res));
                  }
                });

              }).catch(res=>{
                this.loading = false;
                this.$toast('网络错误');
              });

            }).catch(res=>{
              this.loading = false;
              this.$toast('网络错误');
            });

          }).catch(res=>{
            this.loading = false;
            this.$toast('网络错误');
          });
        },
        cmAppPay(tradeid){
          axios({
            url: this.apiUrl + '/wxpayh5/api',
            data: qs.stringify({tradeid:tradeid, ajax:1}),
            method: 'post',
            timeout: 10000
          }).then(res=>{

            this.loading = false;
            if(res.data.errorcode !== 0){
              return this.$toast(res.data.message);
            }
            window.location = res.data.data.url + '&redirect_url=' + encodeURIComponent(location.href);

          }).catch(res=>{
            this.loading = false;
            this.$toast('网络错误');
          });

          return;
          if (typeof js != "undefined" && typeof js.ScanAction != "undefined") {
            try {
              js.ScanAction();
            }catch (e) {
              this.$toast(JSON.stringify(e));
            }
          } else if (typeof window.webkit != "undefined") {
            try {
              window.webkit.messageHandlers.ScanAction.postMessage(null);
            }catch (e) {
              this.$toast(JSON.stringify(e))
            }
          } else {
            this.$toast("ScanAction(...) is not defined.");
          }
        },
        chooseWXPay(config, success, fail, complete){
          setTimeout(() => {
            WeixinJSBridge.invoke(
              'getBrandWCPayRequest',{
                "appId":config.appId,
                "timeStamp":config.timestamp,
                "nonceStr":config.nonceStr,
                "package":config.package,
                "signType":config.signType,
                "paySign":config.paySign
              },
              function(res){
                if(res.err_msg == "get_brand_wcpay_request:ok"){
                  success(res);
                }else if(res.err_msg == "get_brand_wcpay_request:cancel"){
                  fail(res);
                }else{
                  complete(res);
                }
              }
            );
          }, 300);
        },
        unbind(){
          this.modalFlag=true
        },
        init(){
          this.loading = true;
          axios({
            url: this.apiUrl + '/xiche/checkout',
            data: qs.stringify({devcode: this.parameter.devcode, ajax: 1}),
            method: 'post',
            timeout: 10000
          }).then(res=>{
            this.loading = false;
            if(res.data.errorcode !== 0){
              return this.$toast(res.data.message);
            }
            if (typeof res.data.data['tradeid'] != 'undefined') {
              this.$toast(res.data.message);
              setTimeout(()=>{
                this.$router.push({
                  path:'/payok',
                  query:res.data.data
                });
              }, 1000);
              return;
            }
            this.info = res.data.data;
            this.cb = this.info.userInfo.money;
          }).catch(res=>{
            this.loading = false;
            this.$toast('网络错误');
          });
        },
        getUrlParam(paraName) {
          var url = location.search;
          var arrObj = url.split("?");
          if (arrObj.length > 1) {
            var arrPara = arrObj[1].split("&");
            var arr;
            for (var i = 0; i < arrPara.length; i++) {
              arr = arrPara[i].split("=");
              if (arr != null && arr[0] == paraName) {
                return arr[1];
              }
            }
            return "";
          } else {
            return "";
          }
        }
    },
    created(){
        this.parameter = this.$route.query;
        this.init();
    },
    components:{Modal,PayModal, Loading}
}


</script>
<style lang="stylus" scoped>
.profile
  height 100%  
  padding 0 .32rem
  background #F2F2F2
  display flex
  flex-direction column
  .step-img
    background url('../../static/banner.png') no-repeat
    background-size cover
    width 100%
    height 4rem
    margin-top .1rem
  .user-info
    display flex
    flex-direction column
    padding 0 .24rem
    background #ffffff
    border-radius 5px
    margin-top .2rem
    .location-name
      padding .3rem 0
      color #484848
      font-size 14px
    .line
      width 100%
      height 1px
      background #D6D6D6 
    .car-coin
      padding .3rem 0
      color #484848
      font-size 14px  
  .meal
    color #8E8E8E
    font-size 14px
    margin-left .24rem
    margin-top .24rem
  .meal-items
    display flex
    flex-direction row
    align-items center
    padding 0 .24rem
    background #ffffff
    border-radius 5px
    margin-top .05rem
    height 1.2rem 
    .meal-item
      display flex
      flex-direction row
      align-items center
      justify-content center
      border 1px solid  #1EB14A
      color #666666
      font-size 14px
      width 2.4rem
      height .72rem
      border-radius 5px     
  .tag
    margin-top .05rem
    margin-left .24rem
    .tag-txt
      color #8E8E8E
      font-size 11px
      margin-top .02rem
    .tag1
      color #8E8E8E
      font-size 11px
      margin-top .08rem
    .untying
      color #1EB14A
      font-size 11px
      margin-top .28rem
  .money
    display flex
    flex-direction row 
    justify-content center
    color #484848
    font-size 14px
    margin-top 5px
  .gopay
    width 100%
    border-radius 5px
    display flex
    flex-direction row
    justify-content center 
    align-items center
    color #ffffff
    font-size 14px
    background  #1EB14A
    height .9rem 
    margin-top .2rem           
</style>





