<template>
    
    <div class='pay-ok'>
        <div class='ok-info'>
            <i class='iconfont icon-qunfengzhifuchenggong' style="fontSize:1.1rem;color:#1EB14A"></i>
            <div class='ok-txt'>支付完成</div>
        </div>
        <div class='order-profile'>
            <div class='txt'>订单详情</div>
            <div class='profile'>
                <div class='pro-item'>
                    <div class='one'>订单号</div>
                    <div class='two'>{{ info.ordercode }}</div>
                </div>
                <div class='pro-item'>
                    <div class='one'>付款金额</div>
                    <div class='two'>{{ parseFloat(info.money/100).toFixed(2) }}</div>
                </div>
                <div class='pro-item'>
                    <div class='one'>商品</div>
                    <div class='two'>{{ info.uses }}</div>
                </div>
                <div class='pro-item'>
                    <div class='one'>支付状态</div>
                    <div class='two'>{{ info.result }}</div>
                </div>
                <div class='pro-item'>
                  <div class='one'>支付时间</div>
                  <div class='two'>{{ info.paytime }}</div>
                </div>
                <div class='pro-item'>
                  <div class='one'>设备状态</div>
                  <div class='two' v-html="info.dev_status"></div>
                </div>
            </div>
        </div>
        <div class='again-btn' :class="suc?'default':''" @click="load()">连接设备</div>
        <div class='tag'>
            <div class='tag-txt'>提示：设备未响应请重新连接设备</div>
            <div class='tag-txt'>客户服务电话：400-888-3126</div>
        </div>
        <div class='bottom-img' @click="download"></div>
        <Loading v-if="loading"/>
    </div>
</template>
<script>
import axios from 'axios';
import Loading from '@/components/Loading'
import Modal from '@/components/Modal'
import qs from 'qs';

export default {
  data(){
      return{
          apiUrl: 'http://ian.chemi.ren/cmapi/public',
          loading : false,
          tradeid: 0,
          suc: false,
          info: {
            money: 0
          }
      }
  },
  created(){
      this.tradeid = this.$route.query.tradeid;
      this.load();
  },
  methods:{
      download(){
        window.open('http://park.chemi.ren/h5/download.html')
      },
      load(){
        if (!this.tradeid) {
          return this.$toast('初始化参数错误！');
        }
        if (this.loading) {
          return;
        }
        this.loading = true;
        axios({
          url: this.apiUrl + '/xiche/payItem',
          data: qs.stringify({tradeid: this.tradeid, ajax: 1}),
          method: 'post',
          timeout: 10000
        }).then(res=>{
          this.loading = false;
          if(res.data.errorcode !== 0){
            return this.$toast(res.data.message);
          }
          this.info = res.data.data.info;
          this.suc = this.info.status == 1 && this.info.dev_status == '启动成功';
          this.$toast(this.suc ? '连接成功' : '连接失败');
        }).catch(res=>{
          this.loading = false;
          this.$toast('网络错误');
        });
      }
  },
  components:{Modal, Loading}
}
</script>
<style lang="stylus" scoped>
.pay-ok
  display flex
  flex-direction column  
  background #F2F2F2  
  height 100%
  padding 0 .32rem
  .ok-info
    display flex
    flex-direction column
    align-items center
    margin-top .4rem
    .ok-txt
      font-size 14px
      color #1EB14A
      margin-top .2rem   
  .order-profile
    margin-top 0.68rem
    color #8E8E8E
    font-size 14px
    .txt
      margin-left .24rem
    .profile
      margin-top .2rem
      padding .26rem .38rem
      border-radius 5px
      background #fff
      .pro-item
        &:first-child
          margin-top 0  
        display flex
        flex-direction row
        margin-top .1rem
        .one
          width 2.1rem 
  .again-btn
    height .9rem
    background #1EB14A
    color #ffffff
    font-size 14px
    margin-top .4rem 
    border-radius 5px
    display flex
    justify-content center
    align-items center
    &.default
      background #D6D6D6
  .tag
    .tag-txt
      color #8E8E8E
      font-size 10px
      margin-top .06rem 
      margin-left .24rem
  .bottom-img
    background url('../../static/guanggao.png') no-repeat
    background-size cover
    width 100%
    height 3.6rem 
    margin-top .2rem                      
</style>




