import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/pages/Login'
import Profile from '@/pages/Profile'
import PayOk from '@/pages/PayOk'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: Login,
      meta:{
        title:'授权登录'
      }
    },
    {
      path: '/profile',
      component: Profile,
      meta:{
        title:'支付确认'
      }
    },
    {
      path: '/payok',
      component: PayOk,
      meta:{
        title:'支付完成'
      }
    }
  ]
})



