<template>
  <div class="login">
    <div class="title">
      <h2>授权登录</h2>
    </div>
    <div class="vercode-login" v-if="login_type">
      <div class="username">
        <div class="icon">
          <i class="iconfont icon-ziyuan" style="fontSize:.4rem;color:#8E8E8E"></i>
        </div>
        <div class="input">
          <input v-model="name" name="name" type="tel" maxlength="11" placeholder="请输入手机号">
        </div>
      </div>
      <div class="username">
        <div class="icon">
            <i class='iconfont icon-dunpaibaoxianrenzheng_o' style="fontSize:.55rem;color:#8E8E8E"></i>
        </div>
        <div class="input">
          <input v-model="code" name="code" type="tel" maxlength="6" placeholder="请输入验证码">
          <div class="rec-btn" v-show="codeshow" @click="getCode">获取验证码</div>
          <div class="rec-btn" v-show="!codeshow">已发送{{count}}s</div>
        </div>
      </div>
    </div>
    <div class="vercode-login" v-else>
      <div class="username">
        <div class="icon">
            <i class="iconfont icon-ziyuan" style="fontSize:.4rem;color:#8E8E8E"></i>
        </div>
        <div class="input">
          <input v-model="name" name="name" type="tel" maxlength="11" placeholder="请输入手机号">
        </div>
      </div>
      <div class="username" style="marginTop:.2rem">
        <div class="icon">
            <i class="iconfont icon-mima" style="fontSize:.55rem;color:#8E8E8E"></i>
        </div>
        <div class="input">
          <input v-model="password" name="password" type="password" maxlength="30" placeholder="请输入密码">
        </div>
      </div>
    </div>
    <div class="login-btn" @click='goProfile'>
      <span>登录</span>
    </div>
    <div class="info">
      <span>提示：车秘用户可使用账号密码登录</span>
      <div class="change_type" @click="changeModel">{{login_type?"密码登录":"验证码登录"}}</div>
    </div>
    <Loading v-if="loading"/>
  </div>
</template>
<script>
  import axios from 'axios';
  import Loading from '@/components/Loading'
  import qs from 'qs';
  import Cookies from 'js-cookie'

export default {
  data() {
    return {
      apiUrl: 'http://ian.chemi.ren/cmapi/public',
      login_type: true,
      codeshow: true,
      timer: null,
      count: '',
      showtoast: true,
      loading: false,

      name: '',
      code: '',
      password: '',

      parameter: {}
    };
  },
  created(){
    //
    this.parameter.devcode = this.$route.query.devcode || '';
    this.parameter.authcode = this.$route.query.authcode || '';
    this.parameter.token = this.$route.query.token || '';
    if (this.parameter.token) {
      Cookies.set('token', encodeURIComponent(this.parameter.token));
      this.$router.push({
        path: '/profile',
        query: {devcode: this.parameter.devcode, authcode: this.parameter.authcode}
      });
      return;
    }
  },
  components:{Loading},
  methods: {
    changeModel () {
      this.login_type = !this.login_type;
      if (this.login_type) {
        this.password = '';
      } else {
        this.code = '';
      }
    },
    getCode() {
      if (!/[0-9]{11}/.test(this.name)) {
        return this.$toast('手机号不正确');
      }

      if (this.codeshow) {
        // 发送短信
        axios({
          url: this.apiUrl + '/xiche/sendSms',
          data: qs.stringify({telephone: this.name, ajax: 1}),
          method: 'post',
          timeout: 10000
        }).then(res=>{
          if(res.data.errorcode !== 0){
            this.$toast(res.data.message);
          }
        }).catch(res=>{
          this.loading = false;
          this.$toast('网络错误');
        });
      }

      const TIME_COUNT = 60;
      if (!this.timer) {
        this.count = TIME_COUNT;
        this.codeshow = false;
        this.timer = setInterval(() => {
          if (this.count > 0 && this.count <= TIME_COUNT) {
            this.count--;
          } else {
            this.codeshow = true;
            clearInterval(this.timer);
            this.timer = null;
          }
        }, 1000);
      }
    },
    goProfile(){
      if (!/[0-9]{11}/.test(this.name)) {
        return this.$toast('手机号不正确');
      }
      if (this.login_type) {
        if (this.code == '') {
          return this.$toast('请填写验证码');
        }
      } else {
        if (this.password == '') {
          return this.$toast('请填写密码');
        }
      }
      if (this.loading) {
        return;
      }

      var data = {
        __authcode: this.parameter.authcode,
        devcode: this.parameter.devcode,
        ajax: 1,
        telephone: this.name,
        password: this.password,
        msgcode: this.code
      };
      this.loading = true;
      axios({
        url: this.apiUrl + '/xiche/login',
        data: qs.stringify(data),
        method: 'post',
        timeout: 10000
      }).then(res=>{
        this.loading = false;
        if(res.data && res.data.errorcode === 0){
          Cookies.set('token', res.data.data.token);
          this.$toast('登录成功');
          setTimeout(() => {
            this.$router.push({
              path: '/profile',
              query: {devcode: this.parameter.devcode, authcode: this.parameter.authcode}
            });
          }, 1000);
        }else{
          this.$toast(res.data ? res.data.message : '登录失败');
        }
      }).catch(res=>{
        this.loading = false;
        this.$toast('网络错误');
      });
    },
    getUrlParam(paraName) {
      var url = location.search;
      var arrObj = url.split("?");
      if (arrObj.length > 1) {
        var arrPara = arrObj[1].split("&");
        var arr;
        for (var i = 0; i < arrPara.length; i++) {
          arr = arrPara[i].split("=");
          if (arr != null && arr[0] == paraName) {
            return arr[1];
          }
        }
        return "";
      } else {
        return "";
      }
    }
  }
};
</script>
<style lang="stylus" scoped>
.login 
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  background #F2F2F2
  .title 
    margin-top: .8rem;
    h2 
      font-size: 24px;
      color: #8E8E8E;
  .vercode-login
    margin-top  1.16rem
    display flex
    flex-direction column
    .username
      &:last-child
        margin-top:.2rem
      display flex
      flex-direction row
      .icon
        width .98rem
        height .88rem
        display flex
        align-items center
        justify-content center 
        background #fff
        margin-right .04rem
      .input
        display flex
        flex-direction row
        align-items  center
        background #ffffff
        height .88rem
        width 4.64rem
        padding 0 .36rem
        input 
          font-size 14px
          border none
          outline none
          width 3rem
        .rec-btn
          font-size:11px  
          color #1EB14A
  .login-btn
    width 6.36rem
    height .9rem
    border-radius 5px  
    background #1EB14A
    margin-top .4rem
    display flex
    justify-content center
    align-items center
    span 
      color #ffffff
      font-size 14px
  .info
    display flex
    flex-direction row
    justify-content space-between
    width 6.36rem
    margin-top .1rem
    span 
      font-size 14px
      color #8E8E8E  
    .change_type
      font-size 13px
      color #1EB14A 
</style>




